.navbar{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 6vh;
    background-color: #34495e;
    
}
h2{
    color: white;
}
.sorting-buttons{
    position: absolute;
    margin-left: 22%;
    align-items: center;
    justify-content: center;
    /* margin-top: -1px; */
    width:40%;
    
}
button{
    margin:4px;
    width:18%;
    color:white;
    background: transparent;
    border-color: transparent;
    cursor: pointer;
    font-size: 15px;
}
.genbutton{
    align-content: center;
    justify-content: center;
    position: absolute;
    display: flex;
    width:220px;
    height:5%;
    margin-top: 1px;
    margin-right: 10px;
    font-size: 18px;
    font-weight: bold;
    background-color: green;
    border-radius: 8px;
    right: 0;
}
.slider{
    display:inline-block;
    margin-left: 49%;
    width:200px;
    
}
.rc-slider-track{
    background-color: green;
}

.rc-slider-handle,
.rc-slider-handle:active,
.rc-slider-handle:hover,
.rc-slider-handle:focus{
    background-color: green;
    outline:green;
    border:2px solid green;
    box-shadow: green;
}